import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";
import React, { Dispatch, FC } from "react";

interface Props {
  sortMethod: string;
  setSortMethod: Dispatch<React.SetStateAction<string>>;
}

const SortSelect: FC<Props> = ({ sortMethod, setSortMethod }) => {
  return (
    <FormControl
      sx={{
        width: "200px",
        "& .MuiOutlinedInput-root": {
          "&.Mui-focused fieldset": {
            borderColor: "rgb(128,128,128)",
          },
        },
      }}
      size="small"
    >
      <InputLabel
        sx={{
          "&.Mui-focused ": {
            color: "rgb(128,128,128)",
          },
        }}
      >
        
      </InputLabel>
      <Select
        value={sortMethod}
        label=""
        onChange={(e) => setSortMethod(e.target.value)}
      >
        <MenuItem value={"code"}> </MenuItem>
        <MenuItem value={"name"}> </MenuItem>
        <MenuItem value={"rubCourse"}>  </MenuItem>
        <MenuItem value={"eurCourse"}>  </MenuItem>
        <MenuItem value={"usdCourse"}>  </MenuItem>
      </Select>
    </FormControl>
  );
};

export default SortSelect;
